#footer{
    height: 7.5vh;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: stretch;
        align-items: stretch;
    -webkit-box-shadow: 0px -1px 2px rgba(155,155,155,0.2);
            box-shadow: 0px -1px 2px rgba(155,155,155,0.2);
}
.footer-link{
    font-size: 0.8em;
    padding: 0 2.5%;
    color: rgba(0,0,0,0.6);
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    text-align: center;
    -webkit-transition: ease-in-out 0.125s;
    -o-transition: ease-in-out 0.125s;
    transition: ease-in-out 0.125s;
    text-decoration: none;
}
.footer-link:hover{
    cursor: pointer;
    color: rgb(27, 172, 228);
    -webkit-transition: ease-in-out 0.125s;
    -o-transition: ease-in-out 0.125s;
    transition: ease-in-out 0.125s;
}


