.no-data{
	background-color: rgb(0,0,0);
}
#tab-y-labels{
	-ms-flex-preferred-size: 15%;
	    flex-basis: 15%;
	position: relative;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	text-align: center;
	padding: 2.5% 1.25rem 2.5% 0;
}
.past-weight{
	background-color: rgb(200,200,200);
}
.horizontal-scroll-area{
	width: 100%;
	position: relative;
	-ms-flex-preferred-size: 5%;
	    flex-basis: 5%;
}
.horizontal-scroll-background{
	height: 0.65rem;
	border-radius: 10px;
	border: 1px solid rgba(0,0,0,0.1);
	background: rgba(200,200,200, 0.8);
	position: relative;
}
.horizontal-scroll-background:hover{
	cursor: pointer;
}
.horizontal-scroll-bar{
	width: 5%;
	top: 0;
	left: 0;
	height: 100%;
	border-radius: 10px;
	position: absolute;
	background-color: rgba(0,0,0,0.6);
	border: 1px solid rgba(255,255,255,0.1);
	-webkit-transition: left ease-in-out 0.05s;
	-o-transition: left ease-in-out 0.05s;
	transition: left ease-in-out 0.05s;
}
.horizontal-scroll-bar:hover{
	cursor: pointer;
}
@media only screen and (max-width: 775px){
	#weightloss-tab-y-labels, #tab-y-labels{
		display: none !important;
	}
}


